<template>
  <wd-popup
    v-model="showFilter"
    position="bottom"
    custom-class="custom_WotPopup"
  >
    <view class="popup_container">
      <!-- ------------------- Location的的单选框组 ------------------- -->
      <view class="radio_container">
        <text class="radio_title">Loaction</text>
        <wd-radio-group v-model="queryForm.to" shape="dot" inline>
          <view class="radio_oneline">
            <wd-radio :value="'Hong Kong Island'">Hong Kong Island</wd-radio>
            <wd-radio :value="'Kowloon'">Kowloon</wd-radio>
          </view>

          <view class="radio_oneline">
            <wd-radio :value="'Territories'">New Territories</wd-radio>
            <wd-radio :value="'OutLying Islands'">OutLying Islands</wd-radio>
          </view>

          <view class="radio_oneline">
            <wd-radio :value="'Overseas'">Overseas</wd-radio>
          </view>
        </wd-radio-group>
      </view>

      <!-- ------------------- Category的的单选框组 ------------------- -->
      <view class="radio_container">
        <text class="radio_title">Category</text>
        <wd-radio-group v-model="queryForm.methodin" shape="dot" inline>
          <view class="radio_oneline">
            <wd-radio :value="4">Services</wd-radio>
            <wd-radio :value="6">Goods</wd-radio>
          </view>
        </wd-radio-group>
      </view>

      <!-- ------------------- Offering Amount 范围条·滑块 ------------------- -->
      <view class="slide_title">Offering Amount</view>
      <view class="slide_container">
        <wd-slider
          v-model="queryForm.pricein"
          :min="0"
          :max="1000000"
          :hide-min-max="true"
        />
        <view class="silde_min_and_max">
          <text>$0</text>
          <text>$1,000,000</text>
        </view>
      </view>
      <!-- ------------------- Powered by AI Smart Fit 启动按钮 ------------------- -->
      <view class="switch_container">
        <view class="switch_title">[Powered by AI] Smart Fit</view>
        <!-- <view><wd-switch v-model="_checkedPowerByAi" /></view> -->
        <switch color="#6f8fec" />
      </view>
      <!-- ------------------ 底部按钮 ------------------ -->
      <view class="bottom_btn">
        <wd-button
          size="large"
          custom-style="background-color: #469baf; color: #ffffff; margin-top: 40px;"
          @click="handleResetFilter"
          >RESET
        </wd-button>
        <wd-button
          type="primary"
          size="large"
          custom-style="background-color: #469baf; color: #ffffff; margin-top: 40px;"
          @click="handleClickApply"
        >
          APPLY
        </wd-button>
      </view>
    </view>
  </wd-popup>
</template>

<script setup>
import { ref, computed } from "vue"

const props = defineProps({
  modelValue: {
    type: Boolean,
    default: false,
  },
})

const emit = defineEmits(["update:modelValue", "apply", "reset"])

const showFilter = computed({
  get: () => props.modelValue,
  set: (value) => emit("update:modelValue", value),
})

const queryForm = reactive({
  methodin: undefined,
  pricein: 0,
  to: undefined,
})

const handleClickApply = () => {
  emit("apply", {
    ...queryForm,
    pricein: [0, queryForm.pricein],
  })
  showFilter.value = false
}

const handleResetFilter = () => {
  queryForm.methodin = undefined
  queryForm.pricein = 0
  queryForm.to = undefined
  showFilter.value = false
  emit("reset", queryForm)
}
</script>

